.Loading__spinner___3_va1 {
    width: 40px;
    height: 40px;
    border: 4px #ddd solid;
    border-top: 4px #2e93e6 solid;
    border-radius: 50%;
    -webkit-animation: Loading__sp-anime___2CINa 0.8s infinite linear;
            animation: Loading__sp-anime___2CINa 0.8s infinite linear;
}
@-webkit-keyframes Loading__sp-anime___2CINa {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(359deg);
                transform: rotate(359deg);
    }
}
@keyframes Loading__sp-anime___2CINa {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(359deg);
                transform: rotate(359deg);
    }
}
